@import url("https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;600&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;600&display=swap");
html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

button {
  border: none; }

body {
  margin: 0px;
  padding: 0px;
  min-width: 320px;
  overflow-x: hidden;
  font-family: "Work Sans", sans-serif;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  height: 100vh; }

main {
  flex-grow: 1; }

a {
  text-decoration: none;
  color: inherit; }

.container,
.header-container {
  padding: 0 10px; }

.title {
  font-size: 24px; }

.btn {
  display: block;
  text-align: center;
  font-size: 1em;
  width: 150px;
  height: 40px;
  line-height: 40px; }

.btn-primary {
  background-color: #ff3946;
  box-shadow: 0px 5px 10px 0 rgba(255, 57, 70, 0.15);
  color: #ffffff;
  border-radius: 20px; }

.header {
  font-size: 16px; }
  .header__inner {
    margin-top: 6%;
    min-height: 62px;
    display: flex;
    justify-content: space-between;
    align-items: center; }

.menu__wrap {
  position: relative; }

.menu__btn {
  display: block;
  background: none;
  border: none;
  height: 20px;
  margin: 0.5em;
  width: 36px;
  border-radius: 2px; }

.menu__btn:before,
.menu__btn:after {
  background-color: #314584;
  border-radius: 2px;
  content: "";
  display: block;
  height: 2px;
  transition: all 200ms ease-in-out; }

.menu__btn:before {
  box-shadow: 0 6px 0px #314584;
  margin-bottom: 10px; }

.menu__btn-active:before {
  box-shadow: 0 0 0 #314584;
  transform: translateY(6px) rotate(45deg); }

.menu__btn-active:after {
  transform: translateY(-6px) rotate(-45deg); }

.menu__inner {
  flex-direction: column;
  position: fixed;
  left: 0;
  right: 0;
  top: 12%;
  height: 100vh;
  background-color: #ffffff;
  padding: 20px 10px 10px;
  transform: translateX(105%);
  transition: transform 0.5s ease-in;
  display: flex;
  opacity: 0; }

.menu__inner-show {
  transition: transform 0.5s ease-in;
  transform: translateX(0);
  opacity: 1; }

.menu__list {
  order: 2;
  text-align: center;
  margin-top: 50px; }
  .menu__list-item {
    font-size: 1em;
    color: #314584;
    padding: 10px;
    margin: 10px; }

.menu__tools {
  order: 1;
  display: flex;
  justify-content: space-around;
  align-items: center; }

.lang {
  padding-right: 10px;
  color: #314584;
  position: relative;
  font-size: 1em; }
  .lang::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    border: 4px solid transparent;
    border-top: 5px solid #314584; }

.menu__tools-link {
  color: #ff3946;
  border-radius: 6px;
  box-shadow: 0px 5px 10px 0 rgba(25, 45, 51, 0.15); }

.main {
  font-size: 16px; }

.about {
  padding: 12% 0 10%; }

.about__content {
  width: 100%; }

.about__content-title {
  text-align: center;
  font-size: 32px;
  margin-bottom: 7%;
  color: #314584;
  line-height: 1.3; }

.about__content-text {
  text-align: center;
  line-height: 1.67;
  font-size: 1em;
  color: #6e757a;
  margin-bottom: 7%;
  font-weight: 400;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto; }

.about__content-btn {
  display: flex;
  align-items: center;
  justify-content: center; }

.slider__wrap {
  width: 50%; }

.slider__item-img img {
  max-width: 100%;
  width: 90%;
  margin-left: auto; }

.btn-secondary {
  margin-left: 2.1em; }

.slider {
  display: none; }

.slider .slick-dots {
  bottom: -50px; }

.example-work__title {
  width: 100%;
  text-align: center;
  color: #314584;
  margin-bottom: 5%; }

.example-work__inner {
  padding: 5% 0 6%; }

.example-work__block-inner {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 7%; }

.example-work__block-item {
  min-height: 280px;
  max-width: 300px;
  flex: 0 1 100%;
  background-color: #c3d0d8; }

.link-wrap {
  text-align: center; }

.example-work__block-link {
  font-size: 1em;
  color: #ff3946; }

.contactUs__inner {
  padding: 6% 0 5%; }

.contactUs__uptitle {
  color: #ff3946;
  margin-bottom: 3%; }

.contactUs__title {
  color: #314584;
  margin-bottom: 6%; }

.contactUs__form {
  text-align: center; }
  .contactUs__form button {
    margin: 0 auto; }

.contactUs__input-wrap {
  margin-bottom: 4%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.contactUs__input {
  flex-basis: 100%;
  color: #000000;
  padding: 20px 21px 28px 21px;
  border: none;
  background-color: #f4fafe; }
  .contactUs__input:not(:last-child) {
    margin-bottom: 4%; }
  .contactUs__input::-webkit-input-placeholder {
    color: #314584;
    font-family: "Work Sans", sans-serif;
    font-weight: 600;
    font-size: 1em; }
  .contactUs__input::-moz-placeholder {
    color: #314584;
    font-family: "Work Sans", sans-serif;
    font-weight: 600;
    font-size: 1em; }
  .contactUs__input:-ms-input-placeholder {
    color: #314584;
    font-family: "Work Sans", sans-serif;
    font-weight: 600;
    font-size: 1em; }
  .contactUs__input::-ms-input-placeholder {
    color: #314584;
    font-family: "Work Sans", sans-serif;
    font-weight: 600;
    font-size: 1em; }
  .contactUs__input::placeholder {
    color: #314584;
    font-family: "Work Sans", sans-serif;
    font-weight: 600;
    font-size: 1em; }
  .contactUs__input:focus {
    outline: none; }

.contactUs__textarea {
  max-width: 1171px;
  width: 100%;
  border: none;
  background-color: #f4fafe;
  padding: 10px;
  color: #000000;
  margin-bottom: 5%; }
  .contactUs__textarea::-webkit-input-placeholder {
    color: #314584;
    font-family: "Work Sans", sans-serif;
    font-weight: 600;
    font-size: 1em; }
  .contactUs__textarea::-moz-placeholder {
    color: #314584;
    font-family: "Work Sans", sans-serif;
    font-weight: 600;
    font-size: 1em; }
  .contactUs__textarea:-ms-input-placeholder {
    color: #314584;
    font-family: "Work Sans", sans-serif;
    font-weight: 600;
    font-size: 1em; }
  .contactUs__textarea::-ms-input-placeholder {
    color: #314584;
    font-family: "Work Sans", sans-serif;
    font-weight: 600;
    font-size: 1em; }
  .contactUs__textarea::placeholder {
    color: #314584;
    font-family: "Work Sans", sans-serif;
    font-weight: 600;
    font-size: 1em; }
  .contactUs__textarea:focus {
    outline: none; }

.footer {
  font-size: 16px; }

.footer__inner {
  background-color: #314584;
  color: #ffffff;
  padding: 4% 0 3%;
  max-height: 400px;
  text-align: center; }

.footer__upper-tittle {
  font-size: 1em;
  margin-bottom: 1.5em; }

.footer__title {
  margin-bottom: 2.6rem;
  color: #ff3946; }

.footer__nav {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 2.4em; }

.footer__nav-link {
  margin-bottom: 15px;
  margin: 0 10px 15px; }

.copyright {
  font-size: 14px;
  margin-bottom: 2.4em; }

.footer__social a:not(:last-child) {
  margin-right: 4em; }

@media (min-width: 480px) {
  .container,
  .header-container {
    padding: 0 20px; }
  .contactUs__inner {
    padding-left: 10%;
    padding-right: 10%; } }

@media (min-width: 768px) {
  .menu {
    flex-grow: 1;
    margin-left: 4%; }
  .menu__btn {
    display: none; }
  .menu__inner {
    display: flex;
    flex-direction: row;
    transition: none;
    position: initial;
    height: auto;
    background-color: transparent;
    transform: translateX(0);
    padding: 0;
    opacity: 1; }
  .menu__list {
    order: 1;
    display: flex;
    margin-top: 0;
    align-items: center; }
  .menu__list-item {
    margin: 0;
    padding: 0; }
    .menu__list-item:not(:last-child) {
      margin-right: 15px; }
  .menu__tools {
    order: 2;
    margin-left: auto; }
  .lang {
    margin-right: 10%; }
  .slider {
    display: block; }
  .about__inner {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .about__content-title {
    text-align: left; }
  .about__content-text {
    text-align: left;
    max-width: 100%; }
  .about__content {
    max-width: 40%; }
  .about__content-btn {
    justify-content: end; }
  .example-work__block-inner {
    gap: 28px; }
  .example-work__block-item {
    flex: 1 0 40%; }
  .contactUs__inner {
    padding-left: 0;
    padding-right: 0; }
  .contactUs__input-wrap {
    justify-content: space-between; }
  .contactUs__input {
    flex-basis: 49%; }
    .contactUs__input:not(:last-child) {
      margin-bottom: 0; } }

@media (min-width: 992px) {
  .header,
  .footer,
  .main {
    font-size: 18px; }
  .btn {
    width: 202px;
    height: 52px;
    line-height: 52px; }
  .title {
    font-size: 48px; }
  .menu__tools-link:after {
    content: "";
    background: url(../images/Arrow.svg) no-repeat;
    display: inline-block;
    width: 30px;
    height: 15px;
    margin-left: 10px;
    vertical-align: middle; }
  .main__inner {
    padding: 110px 0 192px; }
  .example-work__block-inner {
    margin-bottom: 66px; }
  .example-work__block-item {
    max-width: 370px; }
  .about__content-title {
    font-size: 74px; }
  .contactUs__uptitle {
    margin-bottom: 31px; }
  .contactUs__input-wrap {
    justify-content: space-between; }
  .contactUs__input {
    flex-basis: 45%; }
    .contactUs__input:not(:last-child) {
      margin-bottom: 0;
      margin-right: 4%; } }

@media (min-width: 1200px) {
  .container {
    max-width: 998px;
    margin: 0 auto; }
  .example-work__block-item {
    max-width: 300px; }
  .example-work__title {
    width: 40%;
    text-align: left;
    margin-bottom: 90px; }
  .example-work__block-inner {
    gap: 28px; }
  .menu__list-item:not(:last-child) {
