    margin-top: 64px; }
  .example-work__inner {
    padding: 48px 0 75px; }
  .example-work__block-inner {
    justify-content: space-between; }
  .example-work__block-item {
    max-width: 370px; }
  .example-work__block-inner {
    gap: 24px; }
  .about {
    padding: 110px 0 96px; }
  .container {
    max-width: 1200px;
    margin: 0 auto; }
  .example-work__inner {
    padding: 96px 0 150px; }
  .contactUs__inner {
    padding: 150px 0 80px; } }

@media (min-width: 1600px) {
  .header__inner {
    margin-top: 64px; }
  .header-container {
    max-width: 1440px;
    margin: 0 auto; } }

@media (min-width: 2560px) {
  .header__inner {
    margin-top: 64px; }
  .header-container {
    max-width: 1630px;
    margin: 0 auto; } }
